"use client";

import React, { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';

export default function DashboardPage() {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [studentRA, setStudentRA] = useState('');
  const router = useRouter();

  useEffect(() => {
    // Verificar se o usuário está autenticado
    const authStatus = localStorage.getItem('isAuthenticated');
    const ra = localStorage.getItem('studentRA');
    
    if (authStatus === 'true' && ra) {
      setIsAuthenticated(true);
      setStudentRA(ra);
    } else {
      // Redirecionar para login se não estiver autenticado
      router.push('/login');
    }
  }, [router]);

  const handleLogout = () => {
    localStorage.removeItem('isAuthenticated');
    localStorage.removeItem('studentRA');
    router.push('/login');
  };

  if (!isAuthenticated) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-blue-50 via-white to-indigo-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-8 h-8 border-4 border-blue-600 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-gray-600">Verificando autenticação...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 via-white to-indigo-50 flex flex-col">
      {/* Header */}
      <header className="bg-white shadow-sm border-b">
        <div className="max-w-md mx-auto px-4 py-4 flex items-center justify-between">
          <div className="flex items-center space-x-3">
            <img
              src="https://placehold.co/40x40?text=UFB"
              alt="Logo UFB"
              className="w-10 h-10 rounded-full border-2 border-blue-200"
              onError={(e) => {
                e.currentTarget.src = "https://placehold.co/40x40?text=UFB";
              }}
            />
            <div>
              <h1 className="text-lg font-bold text-gray-800">Portal do Estudante</h1>
              <p className="text-xs text-gray-600">RA: {studentRA}</p>
            </div>
          </div>
          <Button 
            variant="ghost" 
            size="sm" 
            onClick={handleLogout}
            className="text-red-600 hover:text-red-700 hover:bg-red-50"
          >
            Sair
          </Button>
        </div>
      </header>

      {/* Main Content */}
      <main className="flex-1 px-4 py-6">
        <div className="max-w-md mx-auto space-y-6">
          
          {/* Welcome Card */}
          <Card className="bg-gradient-to-r from-blue-500 to-indigo-600 text-white border-0">
            <CardHeader className="text-center">
              <CardTitle className="text-xl">Bem-vindo(a)!</CardTitle>
              <CardDescription className="text-blue-100">
                Ana Carolina Silva
              </CardDescription>
            </CardHeader>
            <CardContent className="text-center">
              <Link href="/student-card">
                <Button size="lg" className="bg-white text-blue-600 hover:bg-blue-50 font-semibold px-8">
                  Visualizar Carteirinha
                </Button>
              </Link>
            </CardContent>
          </Card>

          {/* Quick Access */}
          <div className="space-y-4">
            <h2 className="text-lg font-semibold text-gray-800 text-center">
              Acesso Rápido
            </h2>
            
            <div className="grid gap-4">
              <Link href="/student-card">
                <Card className="hover:shadow-md transition-shadow cursor-pointer">
                  <CardContent className="p-4 flex items-center space-x-3">
                    <div className="w-12 h-12 bg-blue-100 rounded-full flex items-center justify-center">
                      <span className="text-blue-600 font-bold text-lg">🎓</span>
                    </div>
                    <div className="flex-1">
                      <h3 className="font-medium text-gray-800">Carteirinha Digital</h3>
                      <p className="text-sm text-gray-600">Visualize e compartilhe sua carteirinha</p>
                    </div>
                    <div className="text-gray-400">→</div>
                  </CardContent>
                </Card>
              </Link>

              <Card className="opacity-60">
                <CardContent className="p-4 flex items-center space-x-3">
                  <div className="w-12 h-12 bg-gray-100 rounded-full flex items-center justify-center">
                    <span className="text-gray-400 font-bold text-lg">📚</span>
                  </div>
                  <div className="flex-1">
                    <h3 className="font-medium text-gray-500">Histórico Acadêmico</h3>
                    <p className="text-sm text-gray-400">Consulte suas notas e disciplinas</p>
                  </div>
                  <div className="text-xs text-gray-400 bg-gray-100 px-2 py-1 rounded">
                    Em breve
                  </div>
                </CardContent>
              </Card>

              <Card className="opacity-60">
                <CardContent className="p-4 flex items-center space-x-3">
                  <div className="w-12 h-12 bg-gray-100 rounded-full flex items-center justify-center">
                    <span className="text-gray-400 font-bold text-lg">💳</span>
                  </div>
                  <div className="flex-1">
                    <h3 className="font-medium text-gray-500">Cartão Alimentação</h3>
                    <p className="text-sm text-gray-400">Saldo e extrato do RU</p>
                  </div>
                  <div className="text-xs text-gray-400 bg-gray-100 px-2 py-1 rounded">
                    Em breve
                  </div>
                </CardContent>
              </Card>

              <Card className="opacity-60">
                <CardContent className="p-4 flex items-center space-x-3">
                  <div className="w-12 h-12 bg-gray-100 rounded-full flex items-center justify-center">
                    <span className="text-gray-400 font-bold text-lg">📅</span>
                  </div>
                  <div className="flex-1">
                    <h3 className="font-medium text-gray-500">Calendário Acadêmico</h3>
                    <p className="text-sm text-gray-400">Datas importantes e eventos</p>
                  </div>
                  <div className="text-xs text-gray-400 bg-gray-100 px-2 py-1 rounded">
                    Em breve
                  </div>
                </CardContent>
              </Card>
            </div>
          </div>

          {/* Student Info */}
          <Card>
            <CardHeader>
              <CardTitle className="text-center text-lg">Informações do Estudante</CardTitle>
            </CardHeader>
            <CardContent className="space-y-3">
              <div className="flex justify-between">
                <span className="text-sm text-gray-600">RA:</span>
                <span className="text-sm font-medium">{studentRA}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-sm text-gray-600">Curso:</span>
                <span className="text-sm font-medium">Engenharia de Computação</span>
              </div>
              <div className="flex justify-between">
                <span className="text-sm text-gray-600">Semestre:</span>
                <span className="text-sm font-medium">8º Semestre</span>
              </div>
              <div className="flex justify-between">
                <span className="text-sm text-gray-600">Status:</span>
                <span className="inline-block bg-green-100 text-green-800 text-xs px-2 py-1 rounded-full font-medium">
                  Matriculado
                </span>
              </div>
            </CardContent>
          </Card>
        </div>
      </main>

      {/* Footer */}
      <footer className="text-center py-4 px-4 bg-gray-50 border-t">
        <p className="text-xs text-gray-500">
          © 2024 Universidade Federal do Brasil - Portal do Estudante
        </p>
      </footer>
    </div>
  );
}
